.swiper-slide {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: left;
  transition: all 0.3s ease;
  background-position: fixed;
}

.swiper-item {
  width: 70%;
  height: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  position: absolute;
}

.swiper-item::after {
  content: "";
  background-color: black;
  position: absolute;
  width: 100%;
  height: inherit;
  bottom: 0;
  z-index: 1;
  opacity: 0.1;
}

.swiper-item::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 30%;
  top: 0;
  z-index: 1;
  opacity: 0.2;
}

.swiper-slide .number {
  width: auto;
  height: auto;
  position: absolute;
  right: 15px;
  bottom: 10px;
  font-size: 100px;
  -webkit-text-stroke: 1px white;
  color: transparent;
  font-weight: 600;
  font-family: "IBM Plex Sans", sans-serif;
  opacity: 0.3;
}

.swiper-arrows {
  width: 400px;
