.slide-text .btn:before {
  content: "";
  background-color: #fff;
  width: 2px;
  height: 20px;
  position: absolute;
  z-index: 2;
  left: -1px;
  top: -1px;
  border-radius: inherit;
  transition: all 0.3s ease-in-out;
