  justify-content: center;
  background-color: transparent;
  position: absolute;
  border: solid 2px transparent;
  border-left: 0;
  border-bottom: 0;
  transform: rotate(45deg);
  top: 4px;
  left: -10px;
  transition: all 0.3s ease-in-out;
}

